import Vue from 'vue';
import VueRouter from 'vue-router';

import routes from './routes';

import './assets/base.css';

Vue.use(VueRouter);

const router = new VueRouter({
    // mode: 'history',
    routes,
    // scrollBehavior(to, from, savedPosition) {
    //     return {x: 0, y: 0};
    // }
});

new Vue({
    router
}).$mount('#app');



