import Index from './pages/index';
import Album from './pages/album';
import Article from './pages/article';
import Error404 from './pages/404';

export default [
    {
        name: '',
        path: '/',
        component: Index
    },
    {
        name: '',
        path: '/album',
        component: Album
    },
    {
        name: 'article',
        path: '/article',
        component: Article
    },
    {
        name: '404',
        path: '*',
        component: Error404
    }
];



