<template>
    <transition
        v-on:before-enter="beforeEnter"
        v-on:enter="enter"
        v-on:after-enter="afterEnter"

        v-on:before-leave="beforeLeave"
        v-on:leave="leave"
        v-on:after-leave="afterLeave"
        v-bind:css="false"
        mode="out-in"
        type="animation"
    >
        <div class="album" ref="album">
            <h1>
                
            </h1>
            <br>
            <br>
            <hr>
            <router-link to="article"></router-link>
            <div class="first"></div>
            <div class="last"></div>
        </div>
    </transition>
</template>

<script>
import $ from '../lib/zepto';

export default {
    data() {
        return {
            scrollTop: null
        };
    },

    mounted() {
        $(this.$refs.album).on('touchmove', false);
    },

    methods: {

        // 
        beforeEnter: function (el) {
            this.scrollTop = window.scrollY;

            $(el).css({
                'display': 'block',
                'position': 'fixed',
                '-webkit-transform': 'translate3d(100%, 0, 0)',
                'transform': 'translate3d(100%, 0, 0)'
            });
            console.log('beforeEnter', 'album');
        },

        enter: function (el, done) {
            $(el).animate({
                '-webkit-transform': 'translate3d(0, 0, 0)',
                'transform': 'translate3d(0, 0, 0)'
            }, 300, 'ease', () => {
                done()
            });
            console.log('enter', 'album');
        },

        afterEnter: function (el) {
            $(el).css({
                'position': 'absolute',
                '-webkit-transform': 'none',
                'transform': 'none'
            });
            console.log('afterEnter', 'album');
        },

        // 
        beforeLeave: function (el) {
            $(el).css({
                'position': 'fixed',
                '-webkit-transform': 'translate3d(0, 0, 0)',
                'transform': 'translate3d(0, 0, 0)'
            });
            if (this.scrollTop !== null) {
                window.scrollTo(0, this.scrollTop);
                this.scrollTop = null;
            }
            console.log('beforeLeave', 'album');
        },

        leave: function (el, done) {
            $(el).animate({
                '-webkit-transform': 'translate3d(100%, 0, 0)',
                'transform': 'translate3d(100%, 0, 0)'
            }, 300, 'ease', () => {
                done();
            });
            console.log('leave', 'album');
        },

        afterLeave: function (el) {
            $(el).css({
                'display': 'none',
                '-webkit-transform': 'none',
                'transform': 'none'
            });
            console.log('afterLeave', 'album');
        }
    }
}
</script>

<style scoped>
body {
    position: relative; 
}
.album {
    position: absolute; 
    left: 0; 
    top: 0; 
    width: 100%; 
    height: 100%; 
    background-color: #ccc; 
}
.first {
    position: absolute; 
    left: 0; 
    top: 0; 
    width: 100%; 
    text-align: center; 
}
.last {
    position: absolute; 
    left: 0; 
    bottom: 0; 
    width: 100%; 
    text-align: center; 
}
</style>


